<template>
  <div class="field">
    <label class="label"></label>
    <div class="control">
      <label class="radio">
        <input
          type="radio"
          name="answer"
          value=""
          checked
          v-model="tag"
        />
        
      </label>
      <label class="radio">
        <input type="radio" name="answer" value="" v-model="tag" />
        
      </label>
      <label class="radio">
        <input type="radio" name="answer" value="" v-model="tag" />
        
      </label>
      <label class="radio">
        <input
          type="radio"
          name="answer"
          value=""
          v-model="tag"
        />
        
      </label>
    </div>

    <label class="label mt-5"></label>
    <div class="control">
      <input
        class="input"
        type="text"
        placeholder=""
        v-model="title"
      />
    </div>

    <label class="label mt-5"></label>
    <div class="control">
      <textarea
        class="textarea"
        placeholder=""
        rows="10"
        v-model="body"
      ></textarea>

      <button class="button is-info mt-5" type="submit" @click="addPost"></button>
    </div>
  </div>
</template>

<script>
import { db, auth } from '/src/main.js';
export default {
  data() {
    return {
      tag: '',
      title: '',
      body: '',
    };
  },
  methods: {
   async addPost() {
     const ref = await db.collection('posts').doc()
        ref.set(
          {
            id:ref.id,
            user_id: auth.currentUser.uid,
            title: this.title,
            body: this.body,
            tags: this.tag,
            status: '',
            like_count: 0,
            user_name: auth.currentUser.displayName,
            user_photoURL: auth.currentUser.photoURL,
            best_answer:'',
            createdAt: new Date()
          },
          { marge: true }
        );
      this.init();
    },
    init() {
      this.tag = '';
      this.title = '';
      this.body = '';
      this.$store.dispatch('closeModal');
    },
  },
};
</script>

<style scoped></style>
